import Mock from 'mockjs2'
const Random = Mock.Random
import { builder, getQueryParameters } from '../util'

const risk_keys = (options) => {
  const queryParameters = getQueryParameters(options)
  console.log('queryParameters', queryParameters)
  if (queryParameters && !queryParameters.count) {
    queryParameters.count = 4
  }
  const data = []
  for (let i = 0; i < queryParameters.count; i++) {
    const item = Mock.mock({
      value: Random.title(1, 3),
      'type|1': ['港口', '城市', '城市'],
    })
    data.push(item)
  }
  return builder(data)
}

const risks = (options) => {
  const queryParameters = getQueryParameters(options)
  console.log('queryParameters', queryParameters)
  if (queryParameters && !queryParameters.count) {
    queryParameters.count = 5
  }
  const data = []

  // 对应无数据状态
  if(queryParameters.keyword === '0') {
    return builder(data)
  }
  
  for (let i = 0; i < queryParameters.count; i++) {
    const tmpKey = i + 1
    let entityName = ''
    if(i % 2) {
      entityName = `<span style="color:#71141C">${Random.title(1)}</span>, ${Random.title(1)}, ${Random.title(1)}`
    } else {
      entityName = `<span style="color:#71141C">${Random.title(1)}, ${Random.title(1)}</span>, ${Random.title(1)}`
    }
    const item = Mock.mock({
      index: tmpKey,
      'riskLevel|1': ['一级', '二级', '三级', '四级'],
      keyword: `<span style="color:#71141C">${Random.word(5)}</span>`,
      'type|1': ['港口名', '城市名', '城市名'],
      alias: Random.word(4),
      entityName: entityName
    })
    data.push(item)
  }
  return builder(data)
}

Mock.mock(/\/api\/risks/, 'get', risks)
Mock.mock(/\/api\/risk_keys/, 'get', risk_keys)
